/* HEADER : nav bar + titre */

nav {
    position: fixed;
    z-index: 10;
    right: 2.5%;
    top: 2.5%;
    mix-blend-mode: difference;
}

.nav-list {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav-list div {
    width: 10px;
}

.nav-item {
    text-transform: uppercase;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
    color: #f1e0b9;
    letter-spacing: 1px;
}

.lineHoverEffect {
    position: relative;
    margin: 0;
}

.lineHoverEffect span::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 1px;
    bottom: 0;
    left: 0;
    background: #f1e0b9;
    transform: scaleX(0);
    transform-origin: bottom right;
    transition: transform 0.75s ease;
}

.lineHoverEffect:hover span::after {
    transform-origin: bottom left;
    transform: scaleX(1);
}

.Titre {
