/* Hover sur l'image */

.image {
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    transition: .5s ease;
    backface-visibility: hidden;
}

.context1 {
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -70%);
    -ms-transform: translate(-50%, -70%);
    text-align: center;
}

.img3:hover .image {
    opacity: 0.3;
}

.img3:hover .context1 {
    opacity: 1;
}

#texte3 {
    background-color: #F3F3E5;
    padding: 16px 32px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    padding-right: 55%;
