/* formulire */

.formulaire {
    width: 35%;
    margin: auto;
    font-family: 'KarasumaGothic-Regular', sans-serif;
    font-weight: bold;
    color: #8E836B;
}

input[type=text],
select,
textarea {
    width: 100%;
    padding: 15px;
    border: 1px solid #8E836B;
    border-radius: 6px;
    margin-top: 6px;
    margin-bottom: 20px;
    resize: vertical;
    min-height: 20px;
}

input[type=submit],
input[type="reset"] {
    cursor: pointer;
    background-color: #8E836B;
    color: white;
    padding: 15px 60px;
    border: none;
    border-radius: 5px;
}

input[type=submit]:hover,
input[type="reset"]:hover {
    background-color: #8E836B;
    cursor: pointer;
}